<script>
import anime from "animejs";

const setFillValue = el => {
  const stroke = el.getAttribute("stroke");
  const match = /rgb\((\d+),\s*(\d+),\s*(\d+)\)/.exec(stroke);
  return [`rgba(${match[1]},${match[2]},${match[3]}, 0.0)`, stroke];
};

export default {
  inject: ["theme"],
  mounted() {
    this.$nextTick(() => {
      this.setupAnimation();
      this.startAnimation();
    });
  },
  methods: {
    startAnimation() {
      this.timeline.reset();
      this.timeline.seek(0);
      anime.remove(`#${this.id} path`);
      this.timeline = null;
      this.$nextTick(() => {
        this.setupAnimation();
        this.timeline.play();
      });
    },
    setupAnimation() {
      this.timeline = anime.timeline({
        direction: "alternate",
        loop: true,
        easing: "linear"
      });

      this.timeline.add({
        targets: `#${this.id} path`,
        strokeDashoffset: [anime.setDashoffset, 0],
        easing: "easeInOutSine",
        duration: 500,
        delay: function(el, i) {
          return i * 50;
        }
      });

      this.timeline.add({
        targets: `#${this.id} path`,
        easing: "easeInOutSine",
        fill: { value: setFillValue },
        strokeWidth: 0,
        duration: 500,
        offset: "-=300"
      });

      // this.timeline.add({
      //   duration: 100
      // });

      this.timeline.pause();
    }
  },
  computed: {
    textColor() {
      return this.theme.isDark ? "rgb(226, 226, 226)" : "rgb(54,54,54)";
    },
    id() {
      return btoa(Math.random().toFixed(20)).replace(/=/g, "");
    }
  },
  watch: {
    "theme.isDark"() {
      this.$nextTick(() => {
        this.startAnimation();
      });
    }
  }
};
</script>



