<template>
  <v-form ref="form" v-model="valid" lazy-validation>
    <!-- <file-browser/> -->
    <v-text-field
      v-model="name"
      :rules="validateRules.name"
      :counter="maxNameLength"
      :label="$t('repository-editor.name')"
      required
    ></v-text-field>
    <v-text-field
      v-model="password"
      :rules="validateRules.password"
      :counter="maxPasswordLength"
      :label="$t('repository-editor.password')"
      required
      type="password"
    ></v-text-field>
    <v-text-field
      v-model="location"
      :rules="validateRules.location"
      :label="$t('repository-editor.location')"
      required
      placeholder="http://localhost:9000/backup-bucket"
    ></v-text-field>
    <v-text-field
      :counter="20"
      v-model="s3AccessKey"
      :rules="validateRules.s3AccessKey"
      :label="$t('repository-editor.s3-access-key')"
      required
      placeholder="S3EX4MPL3KEYASDFGHJK"
    ></v-text-field>
    <v-text-field
      :counter="40"
      v-model="s3SecretKey"
      :rules="validateRules.s3SecretKey"
      :label="$t('repository-editor.s3-secret-key')"
      required
      type="password"
      placeholder="aS3exaMPLeSeCRE7ACCessK3y+qWertYuI/pzyxC"
    ></v-text-field>
    <v-btn :disabled="!valid" @click="submit">submit</v-btn>
    <v-btn @click="clear">clear</v-btn>
  </v-form>
</template>

<script>
// import FileBrowser from "./FileBrowser";
import isURL from "validator/lib/isURL";

export default {
  components: {
    // FileBrowser
  },
  data: () => ({
    maxNameLength: 100,
    maxPasswordLength: 100,
    valid: true,
    name: "",
    password: "",
    location: "",
    s3AccessKey: "",
    s3SecretKey: ""
  }),
  computed: {
    validateRules() {
      // regex for keys from https://aws.amazon.com/blogs/security/a-safer-way-to-distribute-aws-credentials-to-ec2/
      return {
        s3AccessKey: [
          v => !!v || this.$t("repository-editor.s3-access-key-required"),
          v =>
            (v && /^([A-Z0-9]{20})$/g.test(v)) ||
            this.$t("repository-editor.s3-access-key-invalid")
        ],
        s3SecretKey: [
          v => !!v || this.$t("repository-editor.s3-secret-key-required"),
          v =>
            (v && /^([A-Za-z0-9/+=]{40})$/g.test(v)) ||
            this.$t("repository-editor.s3-secret-key-invalid")
        ],
        location: [
          v => !!v || this.$t("repository-editor.location-required"),
          v => (v && isURL(v)) || this.$t("repository-editor.invalid-url")
        ],
        password: [
          v => !!v || this.$t("repository-editor.password-required"),
          v =>
            (v && v.length <= this.maxPasswordLength) ||
            this.$t("repository-editor.password-length", {
              length: this.maxPasswordLength
            })
        ],
        name: [
          v => !!v || this.$t("repository-editor.name-required"),
          v =>
            (v && v.length <= this.maxNameLength) ||
            this.$t("repository-editor.name-length", {
              length: this.maxPasswordLength
            })
        ]
      };
    }
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        alert(
          JSON.stringify(
            {
              name: this.name,
              password: this.password,
              location: this.location,
              s3AccessKey: this.s3AccessKey,
              s3SecretKey: this.s3SecretKey
            },
            null,
            4
          )
        );
      }
    },
    clear() {
      this.$refs.form.reset();
    }
  }
};
</script>